<template>
  <div class="screen">
    <div class="screen_list_content btn" @click="showSelectNetworkPort">
      <div>{{title}}</div>

      <div>
        <i v-if="1" class="mint-cell-allow-right"></i>
      </div>

    </div>
    <div class="screen_list_text">
      <span>{{text}}</span>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {};
    },
    props: ['title', 'text'],
    methods: {
      //跳转事件
      showSelectNetworkPort() {
        this.$emit('showSelectNetworkPort');
      }
    }
  };
</script>
<style  scoped lang="less">
  .screen {
    // padding: 30px 0 0 0;
    .screen_list_content {
      width: 100%;
      padding: 35px 0 35px 35px;
      display: flex;
      justify-content: space-between;
      background: #383940;
      color: #ddd;
      box-sizing: border-box;
      font-size: 32px;
      margin-top: 10px;
      > div:nth-child(2) {
        position: relative;
      }
    }
    .screen_list_text {
      color: #b1b1b1;
      text-align: left;
      padding: 12px 27px;
      box-sizing: border-box;
      font-size: 20px;
    }
  }
  .mint-cell-allow-right::after {
    width: 16px;
    height: 16px;
    // right: 35px;
  }
  @media screen and (orientation: landscape) {
    .screen {
      .screen_list_content {
        width: 100%;
        padding: 35px;
        display: flex;
        justify-content: space-between;
        background: #383940;
        color: #ddd;
        box-sizing: border-box;
        font-size: 32px;
        margin-top: 8px;
      }
      .screen_list_text {
        color: #b1b1b1;
        text-align: left;
        padding: 12px 27px;
        box-sizing: border-box;
        font-size: 20px;
      }
    }
  }
</style>


