<template>
  <div class="selectScreenMode">
    <vx-header :showback="1" :title="$t('selectScreenMode.selectScreenMode')"></vx-header>
    <div class="vx-container">
      <screenList :title="$t('selectScreenMode.quickScreen')" :text="$t('selectScreenMode.quickScreenText')" @showSelectNetworkPort="showSelectNetworkPort"></screenList>
      <screenList :title="$t('selectScreenMode.qdvancedPointScreenConfiguration')" :text="$t('selectScreenMode.qdvancedPointScreenConfigurationText')" @showSelectNetworkPort="advancedPointScreen"></screenList>
    </div>
  </div>
</template>
<script>
  import screenList from './screenModel_list';
  import htp from "../../api/http";
  import url from '../../api/apiurl';
  import { Indicator, Toast } from 'mint-ui';
  import { setLoc, getLoc, setSen, getSen } from '../../utils';
  export default {
    components: {
      screenList,
      Toast,
      Indicator
    },
    created() {
      this.formatResolution();
      this.getBoxParam();
      // this.getPortSize();
    },
    data() {
      return {
        resolution: {},
        boxParam: {}
      };
    },
    methods: {
      loadamine() {
        if(!window.offline) {
          Indicator.open({
            text: 'Loading...',
            spinnerType: 'triple-bounce'
          });
        }
      },
      getBoxParam() {
        htp.get(url.getBoxParam)
          .then(res => {
            if(res.status === 10000000) {
              this.boxParam = res.data;
              setLoc('boxParam', this.boxParam);
            } else {
              Toast(this.$t('common.requestFailed'));
            }

          });
      },
      //获取箱体的宽度高度及输出分辨率
      formatResolution() {
        htp.get(url.getOutputResolutionRatio)
          .then(res => {
            if(res.status === 10000000) {
              this.resolution.width = res.data.width;
              this.resolution.height = res.data.height;
              this.resolution.rate = res.data.rate;
              setLoc('resolution', this.resolution);
            } else {
              Toast(this.$t('common.requestFailed'));
            }
          });
      },
      //用于获取端口数量
      // getPortSize() {
      //   htp.get(url.getPortSize)
      //     .then(res => {
      //       this.portNumber = res.data.portSize;
      //       setLoc('portNumber', this.portNumber);
      //     });
      // },
      //跳转上一页路由
      showSelectNetworkPort() {
        this.$router.push({ name: 'quickscreen' });
      },
      advancedPointScreen() {
        this.$router.push({ name: 'networkPort_ModalBox' });
      }
    }
  };
</script>
<style scoped lang="less">
  .vx-container {
    top: 100px;
  }
</style>


